import {
  AfterContentInit,
  AfterViewInit,
  Component,
  Inject,
  OnInit,
  ViewChild,
  ViewEncapsulation,
} from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { BsModalRef, BsModalService } from "ngx-bootstrap/modal";
import { DeleteModalComponent } from "../../../partial/modals/delete-modal/delete-modal.component";
import { LocationModalComponent } from "../../../partial/modals/location-modal/location-modal.component";
import { GridOption } from "../../../partial/model/gridOption";
import { AuthService } from "../../../services/auth.service";
import { HttpReqService } from "../../../services/http-req.service";
import { BsDatepickerConfig } from "ngx-bootstrap/datepicker";
import { DatePipe } from "@angular/common";
import { Role } from "../../../services/role";

@Component({
  selector: "app-logs-list",
  templateUrl: "./logs-list.component.html",
  styleUrls: ["./logs-list.component.scss"],
  encapsulation: ViewEncapsulation.None,
  providers: [DatePipe],
})
export class LogsListComponent implements OnInit, AfterContentInit {
  colorTheme = "theme-dark-blue";
  bsConfig: Partial<BsDatepickerConfig>;
  gridOption: GridOption = {
    pagesize: 10,
    search: "",
    filter: {
      dateRange: {
        fromDate: "",
        toDate: "",
      },
      status: { $in: [0, 1] },
    },
    skip: 0,
    limit: 10,
    allrecords: false,
    sortField: "",
    sortOrder: "",
  };
  //displayedColumns: string[] = ['actionOperation', 'objectName', 'userDetails.name', 'createdAt', 'action'];
  displayedColumns: string[] = [
    "actionOperation",
    "userDetails.name",
    "createdAt",
    "action",
  ];
  field = ["createdAt"];
  dataSource = new MatTableDataSource();
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  resultsLength: any;
  data: any;
  statusIndex: any;
  modalRef: BsModalRef;
  organizationArr: any = [];
  gridOption1: GridOption = {
    allrecords: true,
    sortField: "name",
    sortOrder: "asc",
    filter: { status: { $in: [1, 4] } },
  };
  permissionrArr: any = [];
  loading: boolean = false;
  //objectArr: any = ['Device', 'User', 'Organization', 'Login', 'CheckIn'];
  //actionArr: any = ['Create', 'Update', 'Delete', 'Login', 'Checkin', 'Change Request', 'Approval', 'Deactivate']
  userArr: any = [];
  actionOperation: any = "";
  userId: any = "";
  objectName = "";
  isViewed = "";
  fromDate: any = null;
  toDate: any = null;
  isSuccess: any = "";
  organizationId = "";
  tokenData: any;
  roles = Role;
  isOrganizationAdmin: boolean = false;
  isSupperAdmin: boolean = false;
  isDisplay: boolean = false;
  constructor(
    private httpReqService: HttpReqService,
    private modalService: BsModalService,
    private authService: AuthService
  ) {}
  ngOnInit(): void {
    this.loading = true;
    this.dataSource.sort = this.sort;
    this.authService.notificationEvent.subscribe(() => {
      this.gridOption.filter = {
        dateRange: {
          fromDate: "",
          toDate: "",
        },
        status: { $in: [0, 1] },
      };
      this.gridOption.skip = 0;
      this.gridOption.limit = 10;
      if (this.tokenData.role === Role.admin) {
        delete this.gridOption.filter["organizationUniqueId"];
      }
      this.actionOperation = "";
      this.userId = "";
      this.objectName = "";
      this.isViewed = "";
      this.fromDate = null;
      this.toDate = null;
      this.isSuccess = "";
      this.organizationId = "";
      this.getData();
    });
    this.bsConfig = Object.assign(
      {},
      {
        containerClass: this.colorTheme,
        customTodayClass: "custom-today-class",
        dateInputFormat: "DD-MM-YYYY",
      }
    );
    this.tokenData = this.authService.decodeToken();
    this.tokenData = this.tokenData.user;
    this.getOrganizationList();
    this.authService.getBadgeCount();
    if (this.tokenData.role === Role.admin) {
      this.gridOption.filter["organization"] = this.tokenData.organizationId;
    }
    this.getUserList();
    this.getPermissionList();
    this.loading = false;
  }

  ngAfterContentInit() {
    this.dataSource.paginator = this.paginator;

    // this.getData();
  }

  async getData() {
    this.loading = true;
    this.gridOption.sortField = this.sort.active;
    this.gridOption.sortOrder = this.sort.direction;
    this.gridOption.filter["user_id"] = this.tokenData._id;
    const response: any = await this.httpReqService.post(
      "logs/list",
      this.gridOption,
      false
    );
    this.data = response.items;
    this.dataSource = new MatTableDataSource(this.data);
    this.resultsLength = response.totalCount;
    this.loading = false;
  }

  sortChange(): void {
    this.gridOption.limit = this.paginator.pageSize;
    this.gridOption.skip = this.paginator.pageSize * this.paginator.pageIndex;
    this.gridOption.sortField = this.sort.active;
    this.gridOption.sortOrder = this.sort.direction;
    this.paginator.pageIndex = 0;
    this.getData();
  }

  pageChange(): void {
    this.gridOption.limit = this.paginator.pageSize;
    this.gridOption.skip = this.paginator.pageSize * this.paginator.pageIndex;
    this.getData();
  }

  change(event, data, index) {
    this.statusIndex = index;
    if (data.status === 1) {
      const procced = confirm("Do you want to disable this user?");
      if (procced) {
        event.checked = !event.checked;
        if (data.status === 1) {
          this.changeStatus({ status: 0 }, data._id);
        } else {
          this.changeStatus({ status: 1 }, data._id);
        }
      } else {
        event.target.checked = true;
        data.status = 1;
      }
    } else {
      this.changeStatus({ status: 1 }, data._id);
    }
  }

  async changeStatus(data, id) {
    data["_id"] = id;
    const response: any = await this.httpReqService.put("users", data);
    this.dataSource.data[this.statusIndex] = response.items;
    this.dataSource._updateChangeSubscription();
  }

  search(event) {
    let filterValue = event.target.value;
    if (filterValue) {
      filterValue = filterValue.trim(); // Remove whitespace
      filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
      this.gridOption.searchfields = this.field;
    } else {
      delete this.gridOption.searchfields;
      filterValue = "";
    }
    this.gridOption.search = filterValue;
    this.pagesetUp();
    this.getData();
  }

  filterData(event, type) {
    let val = event.target.value;
    if (event.target.value) {
      if (type === "status") {
        val = Number(val);
      }
      this.gridOption.filter[type] = val;
    } else {
      delete this.gridOption.filter[type];
    }
    this.getData();
    this.pagesetUp();
  }

  changeFromDate(e) {
    if (e) {
      const date = e.toISOString().split("T")[0];
      this.gridOption.filter["dateRange"]["fromDate"] = date;
      this.getData();
    }
  }

  changeToDate(e) {
    if (e) {
      const date = e.toISOString().split("T")[0];
      this.gridOption.filter["dateRange"]["toDate"] = date;
      this.getData();
    }
  }

  async getUserList() {
    this.gridOption1.filter["user_id"] = this.tokenData._id;
    const response: any = await this.httpReqService.post(
      "users/list",
      this.gridOption1,
      false
    );
    if (response && response.items.length) {
      this.userArr = response.items;
    }
    this.getData();
  }

  pagesetUp() {
    this.paginator.pageIndex = 0;
    this.gridOption.limit = this.paginator.pageSize;
    this.gridOption.skip = this.paginator.pageSize * this.paginator.pageIndex;
    this.gridOption.sortField = this.sort.active;
    this.gridOption.sortOrder = this.sort.direction;
  }

  async getOrganizationList() {
    this.loading = true;
    const obj = {
      allrecords: true,
      sortField: "organizationName",
      sortOrder: "asc",
      filter: { status: 1 },
    };
    // if (this.tokenData.user.organizationId) {
    //   this.gridOption['orgasnizationUniqueId'] = this.tokenData.user.organizationId;
    // }
    obj.filter["user_id"] = this.tokenData._id;
    const response: any = await this.httpReqService.post(
      "organization/list",
      obj,
      false
    );
    if (response && response.items.length) {
      this.organizationArr = response.items;
    }
    this.loading = false;
  }

  async getPermissionList() {
    this.loading = true;
    let user = this.tokenData;
    // console.log(user);
    if (user != null) {
      if (
        user.is_org &&
        user.organizationId != null &&
        user.organizationId != ""
      ) {
        this.isOrganizationAdmin = true;
      } else if (user.organizationId == null) {
        this.isSupperAdmin = true;
      }
    }
    if (this.isSupperAdmin || this.isOrganizationAdmin) {
      this.isDisplay = true;
    }
    this.loading = false;
  }
}
