import { Component, OnInit, OnDestroy } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Router, RouterEvent, NavigationEnd } from '@angular/router';;
import { GoogleMapsLoaderService } from '../../../services/google-maps-loader.service';
import { HttpReqService } from '../../../services/http-req.service';
/// <reference types="googlemaps" />

@Component({
  selector: 'app-log-detail',
  templateUrl: './log-detail.component.html',
  styleUrls: ['./log-detail.component.scss'],
  providers: [GoogleMapsLoaderService],
})
export class LogDetailComponent implements OnInit, OnDestroy {

  logId: any;
  logData: any;
  dataSource = new MatTableDataSource();
  contectBeforeAction: any;
  contectAfterAction: any;
  userArr: any = ['organizationId', 'email', 'name', 'role', 'nfcEnabled', 'sharedLocation', 'status'];
  deviceArr: any = ['organizationDetail.organizationName', 'id1', 'uid', 'location', 'status'];
  organizationArr: any = ['organizationName', 'registrationNumber', 'organizationId', 'status'];
  checkInArr: any = ['location', 'scanType', 'organizationId', 'status'];
  options: google.maps.MapOptions = {
    center: {
      lat: 37.42,
      lng: -122.103719
    },
    zoom: 7
  };
  route_subscribe: any;
  markerOptions: google.maps.MarkerOptions = { draggable: false };
  selectedLocation: any = {
    lat: 0,
    lng: 0
  };
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private httpReqService: HttpReqService,
    public gmLoader: GoogleMapsLoaderService,
  ) { }

  ngOnInit(): void {
    this.logId = this.route.snapshot.params.id;
    this.getLogDetail();

    this.route_subscribe = this.router.events.subscribe(val => {
      if (val instanceof NavigationEnd) {
        this.logId = this.route.snapshot.params.id;
        this.getLogDetail();
      }
    });
    // this.updatStatus()
  }

  ngOnDestroy(){
    this.route_subscribe.unsubscribe();
  }

  async getLogDetail() {
    const response: any = await this.httpReqService.get('logs', this.logId);
    if (response && response.items.length) {
      this.logData = response.items[0];
      this.contectBeforeAction = this.logData.contectBeforeAction;
      this.contectAfterAction = this.logData.contectAfterAction;
      this.logData.contentBeforeAction.status =
        this.logData.contentBeforeAction.status == 0
          ? "Inactive"
          : this.logData.contentBeforeAction.status == 1
          ? "Active"
          : "Pending";
      this.logData.contentAfterAction.status =
        this.logData.contentAfterAction.status == 0
          ? "Inactive"
          : this.logData.contentAfterAction.status == 1
          ? "Active"
          : "Pending";
      this.dataSource = new MatTableDataSource(this.logData);
      this.updatStatus();
      if (this.logData.objectName == 'CheckIn' && this.logData.location) {
        const location = this.logData.location.split(',');
        this.selectedLocation['lat'] = Number(location[0]);
        this.selectedLocation['lng'] = Number(location[1]);
        this.options.center = this.selectedLocation;
      } else {

      }
    }
  }

  async updatStatus() {
    const response: any = await this.httpReqService.get('logs/updatStatus', this.logId);
  }
}
